import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/pages/index'
import index from '@/pages/index/index'//
import Rgroupdetail from '@/pages/index/rgroupdetail'
import Rgroupinvite from '@/pages/index/rgroupinvite'//
import Sgrouprule from '@/pages/index/sgrouprule'//
import Buy from '@/pages/index/buy'//
import Success from '@/pages/index/success'//
Vue.use(Router)

let router= new Router({
  // mode:'history',
  routes: [
    {
      path: '/',
      name: 'indexs',
      component: Index,
      children:[
        {path:'',component:index,meta:{title:''}}
      ]
    },
    {
      path:'/rgroupdetail',
      name:'rgroupdetail',
      component:Rgroupdetail,
      meta:{
        title:''
      }
    },
    {
      path:'/rgroupinvite',
      name:'rgroupinvite',
      component:Rgroupinvite,
      meta:{
        title:''
      }
    },
    {
      path:'/sgrouprule',
      name:'sgrouprule',
      component:Sgrouprule,
      meta:{
        title:''
      }
    },
    {
      path:'/buy',
      name:'buy',
      component:Buy,
      meta:{
        title:''
      }
    },
    {
      path:'/success',
      name:'success',
      component:Success,
      meta:{
        title:''
      }
    }

  ]
})

////
// to  from next() 
router.beforeEach((to, from, next) => {
  //title
  var flag;
  if(to.matched[0].name != "index"&& to.matched[0].name!='register') {
    flag= common.checkRegisterStatus()
      if(!flag) {
        return
      }
  }
  changeTitle(to.meta.title);
  next();
});

//title
function changeTitle(title) {
  title = title ? title : '';
  window.document.title = title;
};


export default router



